/* eslint-disable jsx-a11y/tabindex-no-positive */
import { useState, useEffect, useRef, lazy } from 'react';
import JoditEditor from 'jodit-react';
import PropTypes from 'prop-types';
import editorBtn from 'assets/images/editorBtn.png';
import Loadable from 'ui-component/Loadable';
const Photo = Loadable(lazy(() => import('views/photo')));

// material-ui
import { Typography, Tooltip, Button, Dialog, DialogTitle } from '@mui/material';
import { CloudQueue as CloudQueueIcon } from '@mui/icons-material';

let shadowObj = {};
function ProductDetail({ localization, finalDescription, onModified, xmlSchema, xmlGetField, langPack }) {
    let [descriptionObj, setDescriptionObj] = useState({});
    let [descriptionTypeObj, setDescriptionTypeObj] = useState({});
    let [cloudImageArr, setCloudImageArr] = useState([]); //
    let [dialogOpen, setDialogOpen] = useState(false); //

    useEffect(() => {
        if (!xmlSchema || finalDescription === undefined) return;
        if (shadowObj.descriptionObj) setDescriptionObj(shadowObj.descriptionObj);
        else getDescriptionObj();
        getDescriptionTypeObj();
        return;
    }, [xmlSchema, finalDescription]);

    function getDescriptionObj() {
        let fieldObj = xmlGetField('superText');
        fieldObj._values = { value: finalDescription };
        return setDescriptionObj(fieldObj);
    } //
    function onDescriptionChange(value) {
        if (!value || !value.trim()) return queryResult(false, ''); //
        if (value === descriptionObj._values.value) return console.log(''); //
        value = value.replace(/\n/g, '').trim();
        let newObj = { ...descriptionObj };
        newObj._values = { value };
        let setter = setDescriptionObj;
        let valueSaver = value.trim().encodeNonASCII();
        if (valueSaver.length > 50000) return queryResult(false, langPack.form.descriptionOverflow);
        newObj.xml.innerHTML = ''; //
        newObj.xml.append(document.createElement('value'));
        newObj.xml.children[0].innerText = valueSaver;
        setter(newObj); //JSX
        onDescriptionTypeChange('2'); //("")
        shadowObj.descriptionObj = newObj;
        return onModified('superText', newObj); //
    } //

    function getDescriptionTypeObj() {
        let fieldObj = xmlGetField('productDescType');
        fieldObj._options = fieldObj.options
            ? fieldObj.options.map((obj) => {
                  return { key: obj.displayName, value: obj.value };
              })
            : []; //
        let value = fieldObj.values ? fieldObj.values[0].value : '2';
        if (fieldObj._options.find((obj) => obj.value === value)) {
            let key = fieldObj._options.find((obj) => obj.value === value).key;
            fieldObj._values = { key, value };
        }
        console.log(fieldObj);
        return setDescriptionTypeObj(fieldObj);
    } //
    function onDescriptionTypeChange(value) {
        let newObj = { ...descriptionTypeObj };
        if (newObj._options.find((obj) => obj.value === value)) {
            newObj._values = { key: newObj._options.find((obj) => obj.value === value).key, value };
        }
        let valueTagName = `value`;
        let setter = setDescriptionTypeObj;
        let valueSaver = `<${valueTagName}>`; //() //type: input
        valueSaver += value;
        valueSaver += `</${valueTagName}>`;
        newObj.xml.innerHTML = valueSaver.trim().encodeHTMLEntity(); //
        setter(newObj); //JSX
        return onModified('productDescType', newObj); //
    } //

    //Cloud-Photo 
    let onDialogClose = (submit) => {
        setDialogOpen(false);
        if (submit) {
            let valueArr = cloudImageArr.map((obj) => {
                return { ...obj, key: obj.id, value: obj.url };
            });
            return onImageChange('add', valueArr);
        }
        return setCloudImageArr([]); //
    }; //
    let onPhotoSelected = (value) => {
        if (!value) return;
        setCloudImageArr(value);
    }; //component

    const editor = useRef(null);
    const config = {
        //
        language: localization.locale === 'zhCN' ? 'zh_cn' : 'en_us',
        enter: 'DIV',
        defaultActionOnPaste: 'insert_clear_html',
        toolbarAdaptive: false,
        buttons: [
            'source',
            'bold',
            'italic',
            'underline',
            'eraser',
            'ul',
            'ol',
            'left',
            'font',
            'fontsize',
            'brush',
            'image',
            'copyformat',
            'copy',
            'paste',
            'table',
            'link',
            {
                iconURL: editorBtn,
                tooltip: 'Clear Image style',
                exec: function (editor, textNearby, obj) {
                    let html = editor.value;
                    html = html.replace(/\n/gi, ''); //inline
                    html = html.replace(/>/gi, '>\n'); //tag 
                    let htmlCodes = html.split('\n');
                    htmlCodes = htmlCodes.map((line) => {
                        if (!/<img src/gi.test(line)) return line;
                        line = / class="magic-(\d{1,3})x000"/gi.test(line)
                            ? line.replace(/x000/gi, '')
                            : line.replace(/ class="magic-(\d{1,3})"/gi, ` class="magic-$1` + `x000"`);
                        return line;
                    }); //smart editing 
                    html = htmlCodes.join('');
                    editor.value = html;
                }
            } /* ,
            {
                iconURL:
                    'data:image/png;base64,',
                tooltip: '',
                exec: function (editor, textNearby, obj) {
                    let varSelected = obj.control.text || '';
                    let varObj = {
                        : '${company_name}',
                        : '${contacts}',
                        : '${email}',
                        : '${salesManager}'
                    };
                    let myVar = varObj[varSelected];
                    editor.selection.removeMarkers();
                    const text = editor.createInside.element('span');
                    text.innerHTML = myVar;
                    editor.selection.insertNode(text);
                    editor.setEditorValue(); // for syncronize value between source textarea and editor
                },
                list: {
                    '${company_name}': '',
                    '${contacts}': '',
                    '${email}': '',
                    '${salesManager}': ''
                }
            } */
        ],
        events: {
            paste: (e) => {
                //
                console.log('...', e); //
                let items = e.clipboardData && e.clipboardData.items;
                if (!items || !items.length) return;
                return console.log(items);
                for (let i = 0; i < items.length; i++) {
                    let item = items[i];
                    if (item == null || item.type.match('image') == null) continue;
                    let file = item.getAsFile();
                    console.log(file);
                    if (file == null) return console.log('');
                    if (file.size > 10 * 1000 * 1000) return boots.queryResult(false, '10M');
                    let fileObj = {
                        name: file.name,
                        size: file.size,
                        url: URL.createObjectURL(file) // blob url
                    };
                    thisInstance.uploadPic(fileObj); //'this' is not referred
                } // file
            },
            beforePasteInsert: (text) => {
                console.log('...'); //
                return;
                setTimeout(() => {
                    editor.value = thisInstance.emailAddFilter(editor.value);
                    console.log(' & ');
                    let imgs = document.images;
                    for (let i = 0; i < imgs.length; i++) {
                        let src = imgs[i].src;
                        if (src.match(/^https:\/\/mail.ali/i) == null) continue;
                        console.log(src);
                        let fileObj = {
                            name: 'image.png',
                            size: '',
                            url: src
                        };
                        thisInstance.uploadPic(fileObj);
                    }
                }, 200);
            } //
        }
    };
    return (
        <>
            <Typography variant="h4" sx={{ paddingTop: '6px', paddingBottom: '6px', color: 'orangered' }}>
                {langPack.steps.productDescriptionTip}
            </Typography>
            <div style={{ display: 'none', flexDirection: 'column', justifyContent: 'center' }}>
                <Tooltip title={langPack.steps.selectFromCloud}>
                    <Button sx={{ height: '100%' }} startIcon={<CloudQueueIcon />} onClick={() => setDialogOpen(true)}>
                        {langPack.steps.cloud}
                    </Button>
                </Tooltip>
                <Dialog onClose={() => onDialogClose(true)} open={dialogOpen}>
                    <DialogTitle>{langPack.steps.selectFromCloud}</DialogTitle>
                    <Photo onPhotoSelected={onPhotoSelected} />
                    <Button variant="contained" color="primary" onClick={() => onDialogClose(true)}>
                        {langPack.common.yes}
                    </Button>
                    <Button variant="contained" color="error" onClick={() => onDialogClose()}>
                        {langPack.common.no}
                    </Button>
                </Dialog>
            </div>
            <JoditEditor
                ref={editor}
                value={descriptionObj._values ? descriptionObj._values.value : ''}
                config={config}
                tabIndex={1} // tabIndex of textarea
                onBlur={(newContent) => onDescriptionChange(newContent)} // preferred to use only this option to update the content for performance reasons
                onChange={(newContent) => {}}
            />
        </>
    );
}

ProductDetail.propTypes = {
    finalDescription: PropTypes.string,
    localization: PropTypes.object,
    xmlSchema: PropTypes.object,
    onModified: PropTypes.func,
    xmlGetField: PropTypes.func,
    langPack: PropTypes.object
};

export default ProductDetail;
